import Const  from './../const';

export default function Reducer(state = {
  page:1,
  pageSize:5,
  totalArticle:5,
  dataArticles:[],
  loading:false
},action) {
  var { dataArticles } = state;
  switch (action.type) {
    case `${Const.GET_ARTICLES}`:
      return state;
    default:
      return state;
  }
}
